export default {
	data() {
		return {
			news: {},
			showNews:false
		}
	},
	onLoad(option) {
		uni.request({
			url: this.prefix + "/getNewsDetail",
			data:{
				id: option.id
			},
			success: (res) => {
				this.news = res.data.data
			}
		})
		setTimeout(()=>{
			this.showNews = true
		},1000)
	},
	methods: {
	}
}
